<!-- 入口列表 -->
<template>
<div id="entrance">
	<ul id="sortable">
		<li class="tile__title" v-for="item in list">{{item.item}}</li>
	</ul>
	<button style="position: fixed; z-index: 9999999; left:0; bottom:0; padding; 10px;" @click="add">Add</button>
</div>
</template>


<script>
import Api from '../../service/api.js'
import { getBaseData } from '../../vuex/actions/common.js'
import Sortable from '../../common/js/util/sortable.js'


var list = []
var i = 0;
for( ; i < 1000; i++ ){
	list.push({
		_index: i,
		item: i
	});
}
export default {
	data() {
		return {
			paths: [], //面包屑导航数据
			path: '',
			list: list,
		}
	},
	vuex: {
		getters: {
			baseData: state => state.common.baseData,
		},
		actions: {
			getBaseData,
		}
	},
	computed: {

		path() {
			return this.$route.path;
		},
	},
	methods: {
		add(){
			// var arr = this.list.concat([]);
			// arr.push({
			// 	_index: i,
			// 	item: i++
			// });
			// arr.reverse();
			var list = []
			var i = 1000;
			for( ; i > 1; i-- ){
				
				list.push({
					_index: i,
					item: i
				});
			}
			console.log(list)
			this.list = list;
		},
	},
	route: {
		activate: function(transition) {

			Vue.nextTick(function () {
				let container = document.getElementById('sortable');
				Sortable.create(container, {
				  animation: 150, // ms, animation speed moving items when sorting, `0` — without animation
				  handle: ".tile__title", // Restricts sort start click/touch to the specified element
				  // draggable: ".tile", 
				});
			});
			transition.next();
		},
	},
	components: {

	}

}


</script>


<style>
@import '../../common/css/sass.scss';
#entrance li{ padding-left: px2rem(10px); border-bottom: 1px solid #eee; line-height: px2rem(80px); }
</style>


